/* Foundry Font Definitions
* Anton Regular for headings and callouts
* Roboto Condensed Bold for subheadings
* Roboto Light for body text
*/

$font__anton: 'Anton', sans-serif;
$font__roboto-condensed: 'Roboto Condensed', sans-serif;
$font__roboto: 'Roboto', sans-serif;
$font__main: sans-serif;
$font__code: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
$font__pre: "Courier 10 Pitch", Courier, monospace;
$font__line-height-body: 1.5;
$font__line-height-pre: 1.6;
